/**
*file style.scss
*/
@font-face {
  font-family: 'sinkin-sans';
  src: url("/assets/fonts/Nobile-BoldItalic.ttf");
}

html, body,
div,
span,
object,
iframe, h1, h2, h3, h4, h5, h6,
p, blockquote,
pre, a, abbr, acronym, address, big, cite, code,
q, strong,
tt, var, dl, dt, dd, ol, ul, li, form, label, legend,
caption, tbody, tfoot, thead, header, footer {
  border: 0;
  font-family: inherit;
  font-size: 100%;
  font-weight: inherit;
  margin: 0;
  padding: 0;
  outline: 0;
}

ul, ol {
  list-style-type: none;
}

a {
  text-decoration: none;
}

a, button {
  cursor: pointer;
}

:focus {
  outline: 0;
}

body {
  font-family: 'Lucida Grande','Hiragino Kaku Gothic ProN',Meiryo,sans-serif;
  background: #ffffff;
  font-size: 16px;
}

@media only screen and (max-width: 480px) {
  body {
    font-size: 14px;
  }
}

.clear-fix {
  content: "";
  clear: both;
  display: table;
}

.inner {
  width: 95%;
  min-width: 760px;
  max-width: 760px;
  margin: 0 auto 20px;
  display: block;
  position: relative;
  clear: both;
  height: auto;
}

select {
  width: 200px;
  height: 5.4vw;
  max-height: 30px;
  min-height: 25px;
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding: 10px 0;
}

.header-content img {
  width: 28%;
  height: auto;
}

.header-title {
  width: 70%;
  height: auto;
  padding: 5px;
  text-align: left;
}

.header-title p {
  margin: 10px 10px 10px 0;
  padding: 25px;
  font-weight: bold;
  font-size: 24px;
  border-radius: 10px;
  border: 2px solid black;
  border-bottom: 3px solid black;
}

.header-title label {
  font-size: 12px;
}

.map-news {
  width: 80%;
  height: 200px;
  margin: 0 0 5px;
  padding-top: 10px;
  text-align: center;
  border: 2px solid black;
}

.map-news p:last-child {
  margin-top: 40px;
  text-align: left;
}

.map-group {
  width: 90%;
  height: auto;
}

.map-group-radio {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 70px;
}

.map-group-radio p {
  font-weight: bold;
}

.map-group-select {
  display: flex;
  justify-content: space-between;
  margin-bottom: 70px;
}

.map-group-select .select1 {
  width: 30%;
}

.map-group-select .select1 label {
  text-align: left;
  font-weight: bold;
}

.map-group-select input {
  max-width: 100%;
  height: 3.4vw;
  max-height: 25px;
  min-height: 25px;
}

.map-group-check {
  display: inline-block;
  position: relative;
  padding-left: 25px;
  margin-bottom: 12px;
  margin-right: 10px;
  cursor: pointer;
  font-size: 18px;
  user-select: none;
  /*Hide the browser's default checkbox*/
  /* On mouse-over, add a grey background color */
  /* When the checkbox is checked, add a blue background */
  /* Show the checkmark when checked */
}

.map-group-check input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

.map-group-check .checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 20px;
  width: 20px;
  background: #eee;
  /* Create the checkmark/indicator (hidden when not checked) */
  /* Style the checkmark/indicator */
}

.map-group-check .checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.map-group-check .checkmark:after {
  left: 5px;
  top: 2px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  transform: rotate(45deg);
}

.map-group-check:hover input ~ .checkmark {
  background-color: #ccc;
}

.map-group-check input:checked ~ .checkmark {
  background-color: #2196F3;
}

.map-group-check input:checked ~ .checkmark:after {
  display: block;
}

.img-pdf {
  vertical-align: middle;
  width: 8%;
}

.btn-submit {
  display: block;
  width: 100px;
  margin: 0 auto;
  text-align: center;
  font-weight: bold;
  padding: 10px;
  background: pink;
  border: 1px solid #eee;
  border-radius: 5px;
}

.input-search {
  background: white;
  padding-left: 5px;
  padding-right: 5px;
  border: 1px solid black;
}

.info-content {
  margin-bottom: 20px;
}

.info-title {
  display: flex;
  align-items: center;
  width: 500px;
  height: 50px;
  margin-bottom: 10px;
  font-size: 28px;
  text-align: left;
  border-radius: 10px;
  border: 2px solid black;
}

.info p {
  margin-bottom: 10px;
}

.info-p-tit {
  font-weight: bold;
  text-align: left;
}

.info-compare {
  font-weight: bold;
  padding: 5px;
  color: black;
  background: white;
  border: 1px solid black;
}

.info-link {
  width: 50%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 20px 0;
}

.info-link a {
  text-decoration: underline;
}

.info-return {
  display: inline-block;
  width: 80px;
  margin-left: 100px;
  text-align: center;
}
